<template>
  <div class="pdf-merge">
    <el-card class="box-card">
      <template #header>
        <div class="card-header">
          <span>PDF合并</span>
          <el-icon><DocumentCopy /></el-icon>
        </div>
      </template>
      
      <el-form :model="form" label-width="120px">
        <el-form-item label="选择PDF文件" required>
          <el-upload
            ref="uploadRef"
            :auto-upload="false"
            :limit="10"
            :on-change="handleFileChange"
            :on-remove="handleFileRemove"
            :before-upload="beforeUpload"
            accept=".pdf"
            multiple
            drag
          >
            <el-icon class="el-icon--upload"><upload-filled /></el-icon>
            <div class="el-upload__text">
              将PDF文件拖到此处，或<em>点击上传</em>
            </div>
            <template #tip>
              <div class="el-upload__tip">
                可以上传多个PDF文件，每个文件不超过100MB
              </div>
            </template>
          </el-upload>
        </el-form-item>

        <el-form-item label="合并顺序">
          <el-input
            v-model="form.mergeOrder"
            placeholder="合并顺序，默认为上传顺序"
            clearable
          />
          <div class="form-tip">
            可以指定文件合并顺序，不填则按上传顺序合并
          </div>
        </el-form-item>

        <el-form-item label="输出文件名">
          <el-input
            v-model="form.outputFileName"
            placeholder="输出文件名，默认为merged.pdf"
            clearable
          />
        </el-form-item>

        <el-form-item>
          <el-button
            type="primary"
            :loading="loading"
            :disabled="selectedFiles.length < 2"
            @click="handleSubmit"
          >
            {{ loading ? '处理中...' : '开始合并' }}
          </el-button>
          <el-button @click="resetForm">重置</el-button>
        </el-form-item>
      </el-form>

      <!-- 文件列表 -->
      <div v-if="selectedFiles.length > 0" class="file-list">
        <el-divider>已选择的文件</el-divider>
        <el-table :data="selectedFiles" style="width: 100%">
          <el-table-column prop="name" label="文件名" />
          <el-table-column prop="size" label="文件大小" width="120" />
          <el-table-column label="操作" width="100">
            <template #default="scope">
              <el-button
                type="danger"
                size="small"
                @click="removeFile(scope.$index)"
              >
                删除
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>

      <!-- 结果显示 -->
      <div v-if="result" class="result-section">
        <el-divider>处理结果</el-divider>
        <el-alert
          :title="result.msg"
          type="success"
          :closable="false"
          show-icon
        />
        <div class="result-info">
          <p><strong>任务ID:</strong> {{ result.data.taskId }}</p>
          <p><strong>合并文件:</strong> {{ result.data.mergedFile }}</p>
          <p><strong>文件大小:</strong> {{ result.data.fileSize }}</p>
          <el-button type="success" @click="downloadResult">
            <el-icon><Download /></el-icon>
            下载结果
          </el-button>
        </div>
      </div>
    </el-card>
  </div>
</template>

<script setup>
import { ref, reactive } from 'vue'
import { ElMessage } from 'element-plus'
import { DocumentCopy, UploadFilled, Download } from '@element-plus/icons-vue'
import { pdfApi } from '@/api'
import { validateFile, formatFileSize } from '@/utils/file'
import { downloadFile } from '@/utils/download'

const uploadRef = ref()
const loading = ref(false)
const selectedFiles = ref([])
const result = ref(null)

const form = reactive({
  mergeOrder: '',
  outputFileName: 'merged.pdf'
})

// 文件选择处理
const handleFileChange = (file, fileList) => {
  const validation = validateFile(file.raw, 'PDF')
  if (!validation.valid) {
    ElMessage.error(validation.errors[0])
    uploadRef.value.clearFiles()
    return
  }
  
  selectedFiles.value = fileList.map(item => ({
    name: item.name,
    size: formatFileSize(item.size),
    raw: item.raw
  }))
}

// 文件移除处理
const handleFileRemove = (file, fileList) => {
  selectedFiles.value = fileList.map(item => ({
    name: item.name,
    size: formatFileSize(item.size),
    raw: item.raw
  }))
}

// 移除文件
const removeFile = (index) => {
  selectedFiles.value.splice(index, 1)
  uploadRef.value.clearFiles()
  selectedFiles.value.forEach(file => {
    uploadRef.value.handleStart(file.raw)
  })
}

// 上传前验证
const beforeUpload = (file) => {
  const validation = validateFile(file, 'PDF')
  if (!validation.valid) {
    ElMessage.error(validation.errors[0])
    return false
  }
  return false // 阻止自动上传
}

// 提交处理
const handleSubmit = async () => {
  if (selectedFiles.value.length < 2) {
    ElMessage.warning('至少需要选择2个PDF文件才能合并')
    return
  }

  loading.value = true
  try {
    const formData = new FormData()
    
    // 添加所有文件
    selectedFiles.value.forEach(file => {
      formData.append('files', file.raw)
    })
    
    formData.append('mergeOrder', form.mergeOrder)
    formData.append('outputFileName', form.outputFileName)

    const response = await pdfApi.mergePdfs(formData)
    result.value = response
    ElMessage.success('PDF合并成功')
  } catch (error) {
    console.error('PDF合并失败:', error)
  } finally {
    loading.value = false
  }
}

// 下载结果
const downloadResult = () => {
  if (result.value?.data?.downloadUrl) {
    try {
      const filename = result.value.data.mergedFile || 'merged.pdf'
      
      // 使用统一的下载工具函数
      downloadFile(result.value.data.downloadUrl, filename)
      
      ElMessage.success('下载已开始')
    } catch (error) {
      console.error('下载失败:', error)
      ElMessage.error('下载失败，请重试')
    }
  } else {
    ElMessage.warning('没有可下载的文件')
  }
}

// 重置表单
const resetForm = () => {
  form.mergeOrder = ''
  form.outputFileName = 'merged.pdf'
  selectedFiles.value = []
  result.value = null
  uploadRef.value?.clearFiles()
}
</script>

<style scoped>
.pdf-merge {
  max-width: 800px;
  margin: 0 auto;
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 18px;
  font-weight: bold;
}

.form-tip {
  font-size: 12px;
  color: #909399;
  margin-top: 5px;
}

.file-list {
  margin-top: 20px;
}

.result-section {
  margin-top: 20px;
}

.result-info {
  margin-top: 15px;
  padding: 15px;
  background-color: #f5f7fa;
  border-radius: 4px;
}

.result-info p {
  margin: 8px 0;
}
</style>
